input::input-placeholder {
    color: #c6c6c6; 
    opacity: 1; 
}
.option{
  position: absolute;
  background: #fff;
  border: 1px solid rgb(211, 211, 211);
  z-index: 2018;
  padding-bottom: 8px;
  padding-top: 8px;
  color: #878787;
  font-size: 13px;
  box-shadow: 0px 2px 6px  rgba(6, 6, 6, 0.3);
}
.option .opp{
  position: relative;
  max-height: 200px;
  overflow-y: auto;
  overflow-x: hidden;
}
.option .arrow{
  position: absolute;
  left: 16px;
  top: -6px;
  transform: rotate(45deg);
  content: ' ';
  width: 10px;
  height: 10px;
  z-index: 2019;
  background: #fff;
  border-left: 1px solid rgb(211, 211, 211);
  border-top: 1px solid rgb(211, 211, 211);
}

.option-item{
  padding: 9px 10px;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.option-item:hover{
  background: #efefef;
  overflow: inherit;
  white-space: inherit;
  text-overflow: inherit;
}
.option-item.select{
  background: #70b5ff;
  color: #fff;
}
.option-item.select:hover{
  background: #70b5ff;
}

/* drag功能的样式 */
.drag-pad{
  position: absolute;
  width: 10px;
  left: calc(100% - 5px);
  z-index: 101;
  background: transparent;
  cursor: col-resize;
}
.drag_line{
  position: absolute;
  height: 100%;
  z-index: 108;
  width: 1px;
  background: #a8a8a8;
  top: 0px;
}
.hover-show{
  position: absolute;
  z-index: 2017;
  background: rgb(250, 250, 250);
  border: 1px solid #dadada;
  padding: 10px;
  font-size: 12px;
  color: rgb(65, 183, 238);
  box-shadow: 0px 1px 10px rgba(182, 182, 182, 0.5);
}
.hover-show:before{
  content: ' ';
  position: absolute;
  top: calc(100% - 5px);
  left: 50%;
  z-index: 2017;
  width: 10px;
  height: 10px;
  transform: translateX(-50%) rotate(45deg);
  background: rgb(250, 250, 250);
  border-bottom: 1px solid #dadada;
  border-right: 1px solid #dadada;
}
.sort-wrap{
  position: absolute;
  left: calc(100% - 20px);
  width: 14px;
  height: 40px;
  background: transparent;
  cursor: pointer;
  top: 50%;
  transform: translateY(-50%);
}
.sort-wrap .fa-sort-asc,
.sort-wrap .fa-sort-desc {
  position: absolute;
  font-size: 16px;
  color: silver;
}
.sort-wrap .fa-sort-asc{
  top: 11px;
}
.sort-wrap .fa-sort-desc{
  bottom: 12px;
}
.sort-wrap .fa-sort-asc.active,
.sort-wrap .fa-sort-desc.active {
  color: #2988e7;
}
.page{
  padding-top: 8px; 
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #d8d8d8;
}
.page-arrow{
  font-size: 16px;
  display: inline-block;
  margin-left: 12px; 
  margin-right: 12px; 
  cursor: pointer;
  width: 16px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  background: transparent;
  color: #999;
  border-radius: 50%;
}
.page-arrow:hover{
  color: #fff;
  background: #4eac20;
}
.search-text{
  height: 26px;
  line-height: 26px;
  text-indent: 2px;
  color: #999;
  position: relative;
  margin-bottom: 5px;
}
.search-text span{
  position: absolute;
  left: 7px;
  top: 2px;
  z-index: 99;
  height: 22px;
  background: transparent;
  line-height: 22px;
  width: 20px;
  font-size: 12px;
  color: #3fa50d;
}
.search-text input{
  font-size: 12px;
  font-weight: bold;
  color: #2988e7;
  position: absolute;
  left: 4px;
  top: 0px;
  height: 100%;
  width: calc(100% - 8px);
  z-index: 98;
  border: 1px solid #c7cfd1;
  outline: none;
  text-indent: 26px;
}
.search-text input:hover,
.search-text input:focus{
  border: 1px solid #3fa50d;
  outline: none;
}
.add-mark{
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(-50%);
  color: rgb(18, 214, 18);
  font-size: 18px;
}
.add-line-icon{
  position: absolute;
  left: -7px;
  font-size: 12px;
  z-index: 1992;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  line-height: 16px;
  background: #27d413;
  text-align: center;
  cursor: pointer;
}
.add-line-icon:hover{
  background: #169b07;
}
.add-line-icon .fa{
  color: #fff;
}
.no-text{
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  color: #a7a7a7;
  font-family: cursive;
}
</style>



